<template>
  <div class="login">

    <!-- 顶部导航栏 -->
    <van-nav-bar left-text="返回" right-text="先看下商品" @click-right="goState('Home')">
      <template #left>
        <div class="dflex">
          <div class="logo"></div>
          <div class="logo-text">Luckin Coffee</div>
        </div>
      </template>
    </van-nav-bar>

    <!-- 提示信息 -->
    <div class="prompt">
      <p>欢迎回来</p>
      <p>Please login to your accounts</p>
    </div>

    <!-- 输入表单 -->
    <van-form @submit="onSubmit">
      <!-- 账号 -->
      <van-field
        v-model="userInfo.phone"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写手机号' }]"
      />
      <!-- 密码 -->
      <van-field
        v-model="userInfo.password"
        name="密码"
        label="密码"
        placeholder="密码"
        :type="isRegisterPassword ? 'password' : 'text'"
        :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
        autocomplete="off"
        @click-right-icon="toggleRegsiterPasswordType"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <!-- 忘记密码 -->
      <div class="forgot">
        <span @click="goState('Forgot')">忘记密码?</span>
      </div>

      <div class="pt-30px" style="margin: 16px;">
        <van-button round block type="info" native-type="submit" @click="login()">登录</van-button>
      </div>

      <div style="margin: 16px;" @click="showPopup">
        <van-button round block type="info" native-type="submit">注册</van-button>
      </div>
    </van-form>
    <!-- 注册区域 -->
    <van-popup v-model="show" position="bottom" round :style="{ height: '45%' }" closeable>
      <div class="register-title">注册</div>
      <van-form>
        <van-field
          v-model="userRegisterInfo.phone"
          name="手机号"
          label="手机号"
          placeholder="11位手机号"
          autocomplete="off"
        />

        <van-field
          v-model="userRegisterInfo.password"
          :type="isRegisterPassword ? 'password' : 'text'"
          name="密码"
          label="密码"
          placeholder="字母数字_组合且字母开头(6-16位)"
          :right-icon="isRegisterPassword ? 'closed-eye' : 'eye-o'"
          autocomplete="off"
          @click-right-icon="toggleRegsiterPasswordType"
        />

        <van-field
          v-model="userRegisterInfo.nickName"
          name="昵称"
          label="昵称"
          placeholder="字母数字_汉字组合(1-10位)"
          autocomplete="off"
        />

        <div class="commit-btn register-btn">
          <van-button round block color="#0C34BA" @click="register">注册</van-button>
        </div>
      </van-form>
    </van-popup>
  </div>
</template>
<script>
import { Toast } from "vant";
import { validForm } from "../assets/js/validForm";
export default {
  name: "Login",
  data() {
    return {
      //用户登录信息
      userInfo: {
        phone: "",
        password: ""
      },
      //用户注册信息
      userRegisterInfo: {
        phone: "",
        password: "",
        nickName: ""
      },
      isRegisterPassword: true,
      show: false,
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    //切换注册密码框的类型
    toggleRegsiterPasswordType() {
      this.isRegisterPassword = !this.isRegisterPassword;
    },
    register() {
      //构造表单验证信息
      let o = {
        phone: {
          value: this.userRegisterInfo.phone,
          errorMsg: "手机号格式不正确",
          reg: /^1[3-9]\d{9}$/
        },
        password: {
          value: this.userRegisterInfo.password,
          errorMsg: "密码格式不正确",
          reg: /^[A-Za-z]\w{5,15}$/
        },
        nickName: {
          value: this.userRegisterInfo.nickName,
          errorMsg: "昵称格式不正确",
          reg: /^[\w\u4e00-\u9fa5]{1,10}$/
        }
      };

      //表单验证
      let isPass = validForm.valid(o);

      if (isPass) {
        let userInfo = Object.assign({}, this.userRegisterInfo);
        userInfo.appkey = this.appkey;
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true
        }),
          //发起注册请求
          this.axios({
            method: "POST",
            url: "/register",
            data: userInfo
          })
            .then(result => {
              if (result.data.code == 100) {
                this.$toast.clear();
                this.$toast.success("注册成功");
              }
            })
            .catch(err => {
              console.log("err ==> ", err);
            });
      }
    },
    //登录
    login() {
      //构造表单验证信息
      let o = {
        phone: {
          value: this.userInfo.phone,
          errorMsg: "手机号格式不正确",
          reg: /^1[3-9]\d{9}$/
        },
        password: {
          value: this.userInfo.password,
          errorMsg: "密码格式不正确",
          reg: /^[A-Za-z]\w{5,15}$/
        }
      };

      let isPass = validForm.valid(o);
      if (isPass) {
        //发起登录请求
        //复制用户注册信息
        let userInfo = Object.assign({}, this.userInfo);
        userInfo.appkey = this.appkey;
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0
        });

        //发起注册请求
        this.axios({
          method: "POST",
          url: "/login",

          data: userInfo
        })
          .then(result => {
            // 清除轻提示
            this.$toast.clear();
            if (result.data.code == 200) {
              //登录成功
              //保存token, 以便后面验证登录
              localStorage.setItem("__tk", result.data.token);
              // 跳转到主页
              this.goState("Home")
            } else {
            // 提示登录出错
              this.$toast(result.data.msg);
            }
          })
          .catch(err => {
            this.$toast.clear();
            console.log("err ==> ", err);
          });
      }
    },
    goState(name) {
        this.$router.push({name});
      }
  }
};
</script>
<style lang="less" scoped>
.commit-btn {
  margin-top: 20px;
}
.dflex {
  display: flex;
}
.pt-30px {
  padding-top: 30px;
}
.forgot {
  text-align: right;
  color: #666666;
  font-size: 14px;
  margin-top: 20px;
  padding-right: 15px;
}
.logo {
  width: 34px;
  height: 34px;
  background-color: #000;
  border-radius: 50%;
  margin-top: 6px;
}

.logo-text {
  font-size: 16px;
  font-weight: bold;
  margin-left: 10px;
  line-height: 46px;
}
.prompt {
  padding: 50px;
}
.prompt p:first-child {
  font-size: 30px;
}
.prompt p:last-child {
  font-size: 18px;
}
.register-title {
  font-size: 24px;
  font-weight: bold;
  padding: 0 15px;
  margin-bottom: 30px;
}
.register-btn {
  padding: 0 15px;
}
</style>